/* GallerySection.css */
.gallery-section {
  background-color: rgba(24, 103, 33, 0.8);
  background-size: cover;
  background-position: center;
  padding: 40px 20px;
}

.gallery-section h2 {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 20px;
  color: rgb(246, 244, 244);
}

.gallery-images {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 10px; /* 调整图片间距为 10px */
}

.gallery-images img {
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 10px;
  margin: 5mm; /* 每个图片直接有 5mm 的距离 */
}

.gallery-btn-container {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.gallery-btn {
  padding: 10px 20px;
  border: 2px solid rgba(255, 255, 255, 0.8); /* 透明底色边框 */
  background-color: transparent;
  color: rgba(255, 255, 255, 0.8);
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.gallery-btn:hover {
  background-color: rgba(255, 255, 255, 0.8);
  color: rgba(24, 103, 33, 0.8);
}

/* Media query for responsive design */
@media (max-width: 768px) {
  .gallery-section {
    padding: 20px 10px; /* 调整移动设备上的内边距 */
  }

  .gallery-section h2 {
    font-size: 1.2rem; /* 调整移动设备上的字体大小 */
    margin-bottom: 15px; /* 调整移动设备上的边距 */
  }

  .gallery-images img {
    width: 100px; /* 调整移动设备上的图片宽度 */
    height: 100px; /* 调整移动设备上的图片高度 */
    margin: 1.5mm; /* 调整移动设备上的图片间距 */
  }

  .gallery-btn {
    font-size: 0.875rem; /* 调整移动设备上的字体大小 */
    padding: 8px 16px; /* 调整移动设备上的按钮内边距 */
  }
}
