/* AboutSection.css */
.about {
  padding: 50px 20px;
  text-align: center;
}

.about .container {
  max-width: 800px;
  margin: 0 auto;
}

.aboutlogo {
  font-family: 'Luckiest Guy', cursive;
  font-size: 3rem;
  color: #000000;
}

.about p {
  font-size: 1rem;
  line-height: 1.6;
}

/* Media query for responsive design */
@media (max-width: 768px) {
  .about {
    padding: 30px 10px; /* 调整移动设备上的内边距 */
  }

  .about .container {
    max-width: 90%; /* 调整移动设备上的最大宽度 */
  }

  .aboutlogo {
    font-size: 2rem; /* 调整移动设备上的字体大小 */
  }

  .about p {
    font-size: 0.875rem; /* 调整移动设备上的字体大小 */
    line-height: 1.4; /* 调整移动设备上的行高 */
  }
}
