/* EventsSection.css */
.events-section {
  background-color: #f9f9f9;
  padding: 40px 20px;
  text-align: center;
}

.events-section h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.events-container {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.event-card {
  background: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 20px;
  max-width: 300px;
  width: 100%;
  text-align: left;
}

.event-card h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.event-card p {
  font-size: 1rem;
  color: #333;
}

/* Media query for responsive design */
@media (max-width: 768px) {
  .events-section {
    padding: 30px 10px; /* 调整移动设备上的内边距 */
  }

  .events-section h2 {
    font-size: 1.5rem; /* 调整移动设备上的字体大小 */
    margin-bottom: 15px; /* 调整移动设备上的边距 */
  }

  .events-container {
    gap: 10px; /* 调整移动设备上的间距 */
  }

  .event-card {
    padding: 15px; /* 调整移动设备上的内边距 */
    max-width: 60%; /* 调整移动设备上的最大宽度 */
  }

  .event-card h3 {
    font-size: 1rem; /* 调整移动设备上的字体大小 */
    margin-bottom: 8px; /* 调整移动设备上的边距 */
  }

  .event-card p {
    font-size: 0.875rem; /* 调整移动设备上的字体大小 */
  }
}
